<template>
	<div>
		<h1 style="text-align: center;">Tokyo Toshokan</h1>
		<h2 style="text-align: center;">東京 図書館</h2>
		<hr>

		<div class="searchdiv">
			<el-input type="text" name="fh" id="fh" placeholder="请输入内容">
				<el-button @click="search()" slot="append" icon="el-icon-search"></el-button>
			</el-input>
		</div>


		<div id='list'>
			<div class="listson"></div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'tank',
		props: {
			msg: String
		},
		data() {
			return {
				data: {
					url: "https://www.tokyotosho.info/search.php?terms=" + "" +
						"&type=0&searchName=true&searchComment=true&size_min=&size_max=&username=",
					method: "GET",
					header: null,
					mediaType: null,
					mediaTypeData: null
				},
			}
		},
		beforeCreate() {
			document.title = 'xiaohu'
		},
		mounted() {

		},
		methods: {
			search() {
				var other = this
				var fh = document.getElementById("fh");
				this.data.url = "https://www.tokyotosho.info/search.php?terms=" + fh.value +
					"&type=0&searchName=true&searchComment=true&size_min=&size_max=&username="
				this.$http({
					method: "post",
					url: this.global.proxyapi2,
					data: this.data,
					headers: {
						'Content-Type': 'application/json;charset=UTF-8'
					}
				}).then(function(response) {
					other.setDom(response.data)
				}).catch(function(error) {
					console.log(error);
				});
			},
			setDom(data) {
				var list = document.getElementById("list");
				var listson = document.body.querySelectorAll('.listson');
				list.removeChild(listson[0])
				var newDom = data.slice(data.indexOf('<table class="listing">'), data.indexOf(
					'<p style="text-align: center">'))
				var div = document.createElement('div');
				div.className = 'listson'
				div.innerHTML = newDom;
				list.appendChild(div);
				var spritemagnet = document.body.querySelectorAll('.sprite_magnet');
				for (var i = 0; i < spritemagnet.length; i++) {
					spritemagnet[i].innerText = '☞磁力链接下载'
				}

				var adom = document.getElementsByTagName('a')
				for (var i = 0; i < adom.length; i++) {
					// nofollow
					if (adom[i].rel == 'nofollow') {
						adom[i].href = '#'
					}
					// <a href="?username=Tsundere-Raws">Tsundere-Raws</a>
					var urlt1 = '?username=' + adom[i].innerText
					var urlt2 = adom[i].href.slice(adom[i].href.indexOf('?'))
					if (urlt1 == urlt2) {
						adom[i].href = '#'
					}
				}
			}
		}
	}
</script>

<style>
	.web {
		display: none;
	}


	.searchdiv {
		margin-top: 20px;
		margin-bottom: 5%;
	}

	.sprite_magnet {
		color: red;
	}
</style>
