<template>
	<div class="hello">
		<h1>pixiv排行榜</h1>
		<el-row :gutter="20">
			<el-col :span="12">
				<div class="grid-content bg-purple">
					<el-upload class="upload-demo" action="https://jsonplaceholder.typicode.com/posts/"
						:on-preview="handlePreviewF" :on-remove="handleRemoveF" :file-list="fileListF"
						:before-upload="beforeuploadF" list-type="picture-card">
						<el-button size="small" type="primary">点击上传</el-button>
						<div slot="tip" class="el-upload__tip">只能上传图片</div>
					</el-upload>
				</div>
			</el-col>
			<el-col :span="12">
				<div class="grid-content bg-purple">
					<el-upload class="upload-demo" action="https://jsonplaceholder.typicode.com/posts/"
						:on-preview="handlePreview" :on-remove="handleRemove" :file-list="fileList"
						:before-upload="beforeupload" list-type="picture-card">
						<el-button size="small" type="primary">点击上传</el-button>
						<div slot="tip" class="el-upload__tip">只能上传图片</div>
					</el-upload>
				</div>
			</el-col>
		</el-row>
		<el-button v-show="!fullscreenLoading" type="primary" @click="startRun()">
			生成图片
		</el-button>
		<el-button v-show="fullscreenLoading" type="primary">
			生成中……
		</el-button>
		<el-divider></el-divider>
		<div id="myimgtank">
			<template v-for="(ocol,icol) in tankimg"><img :src="tankimg[icol]" class="tank"></template>
		</div>
		<el-divider></el-divider>
		<el-button v-if="tankimg.length!=0" type="primary" @click="downloadImg()">
			全部下载
		</el-button>
	</div>
</template>

<script>
	export default {
		name: 'tank',
		props: {
			msg: String
		},
		data() {
			return {
				fileListF: [],
				fileList: [],
				tankimg: [],
				fileListitem: false,
				newdate: false,
				widthF: null,
				heightF: null,
				imgF: null,
				AInit: 1,
				RInit: 3,
				GInit: 6,
				BInit: 1,
				lightInitF: 1,
				lightInitB: 0.3,
				colourInit: 0.2,
				canvasF: null,
				canvasFArr: null,
				provinceId: null,
				fullscreenLoading: false
			}
		},
		beforeCreate() {
			document.title = 'xiaohu'
		},
		mounted() {

		},
		methods: {
			handleRemoveF(file, fileList) {},
			handlePreviewF(file) {},
			beforeuploadF(file) {
				var isJPG = (file.type === 'image/jpeg' ||
					file.type === 'image/png' ||
					file.type === 'image/gif' ||
					file.type === 'image/tiff' ||
					file.type === 'image/x-icon' ||
					file.type === 'image/vnd.wap.wbmp' ||
					file.type === 'image/fax' ||
					file.type === 'image/pnetvue' ||
					file.type === 'image/vnd.rn-realpix');
				if (!isJPG) {
					this.$message.error('上传图片格式!');
					return false;
				}
				this.fileListF = [];
				let newimgvar = {
					name: file.name,
					url: window.URL.createObjectURL(file)
				}
				this.fileListF.push(newimgvar)
				return false;
			},
			handleRemove(file, fileList) {
				if (!this.fileListitem) {
					this.fileList = fileList
				}
				this.fileListitem = false
			},
			handlePreview(file) {},
			beforeupload(file) {
				var isJPG = (file.type === 'image/jpeg' ||
					file.type === 'image/png' ||
					file.type === 'image/gif' ||
					file.type === 'image/tiff' ||
					file.type === 'image/x-icon' ||
					file.type === 'image/vnd.wap.wbmp' ||
					file.type === 'image/fax' ||
					file.type === 'image/pnetvue' ||
					file.type === 'image/vnd.rn-realpix');
				if (!isJPG) {
					this.$message.error('上传图片格式!');
					return false;
				}
				let newimgvar = {
					name: file.name,
					url: window.URL.createObjectURL(file)
				}
				this.fileList.push(newimgvar)
				this.fileListitem = true
				return false;
			},
			imgLook(icol) {},
			downloadImg() {
				for (var i = 0; i < this.tankimg.length; i++) {
					var url = this.tankimg[i];
					var a = document.createElement('a');
					var event = new MouseEvent('click')
					a.download = 'tank_' + i + "_" + new Date().getTime()
					a.href = url;
					a.dispatchEvent(event)
				}
			},
			startRun() {
				if(this.fileListF.length*this.fileList.length==0){
					this.$message.error('先选择图片吧');
					return;
				}
				this.tankimg = []
				this.fullscreenLoading = true;
				const that = this
				//表
				this.GetGrayScale(this.AInit, this.RInit, this.GInit, this.BInit, this.lightInitF,
					this.fileListF[0].url,
					function(data) {
						that.canvasF = data;
						that.widthF = data.width;
						that.heightF = data.height;
						// var imgup = document.getElementsByClassName('imgup')
						for (var i = 0; i < that.fileList.length; i++) {
							//里
							that.GetGrayScaleHalf(that.lightInitB, that.fileList[i].url, function(data) {
								that.setPng(data)
							})
						}
					})
			},
			/**
			 * 表图缩放
			 * @param {Object} dataF
			 * @param {Object} w
			 * @param {Object} h
			 * @param {Object} newW
			 * @param {Object} newH
			 */
			/**
			 * 输出图片
			 * @param {Object} canvasB
			 */
			setPng(canvasB) {
				if (this.canvasF == null) return;
				if (canvasB == null) return;
				var base64 = this.GrayMergeHalf(this.canvasF, canvasB)
				// var img = document.createElement("img");
				// img.src = base64;
				// img.className = 'tank'
				// var base = document.body.querySelector('#myimgtank');
				// base.appendChild(img);
				this.tankimg.push(base64)
				if (this.tankimg.length == this.fileList.length) {
					this.fullscreenLoading = false;
				}
			},
			/**
			 * 表图：修改色差
			 * @param {Object} A
			 * @param {Object} R
			 * @param {Object} G
			 * @param {Object} B
			 * @param {Object} Light
			 * @param {Object} imgUrl
			 * @param {Object} callback
			 */
			GetGrayScale(A, R, G, B, Light, imgUrl, callback) {
				var img = new Image();
				img.src = imgUrl;
				var canvas = null;
				img.onload = function() {
					var width = img.width;
					var height = img.height;
					canvas = document.createElement('canvas');
					var ctx = canvas.getContext('2d');
					canvas.width = width;
					canvas.height = height;
					// 将源图片复制到画布上
					ctx.drawImage(img, 0, 0, width, height);
					// 获取画布的像素信息
					var imageData = ctx.getImageData(0, 0, width, height);
					var data = imageData.data;
					// 对像素集合中的单个像素进行循环，每个像素是由4个通道组成，所以要注意
					var i = 0;
					var rf = R / (R + G + B);
					var gf = G / (R + G + B);
					var bf = B / (R + G + B);
					this.canvasFArr = new Array(height);
					var canvasFArrSon = new Uint8ClampedArray(width * 4);
					var x = 0;
					var y = 0;
					while (i < data.length) {
						var ri = data[i++],
							gi = data[i++],
							bi = data[i++],
							ai = data[i++];
						var gray = (ri * rf + gi * gf + bi * bf) * Light;
						if (gray > 255) {
							gray = 255;
						}
						var alpha = ai * A;
						if (alpha > 255) {
							alpha = 255;
						}
						data[i - 4] = gray;
						data[i - 3] = gray;
						data[i - 2] = gray;
						data[i - 1] = alpha;

						canvasFArrSon[x++] = gray;
						canvasFArrSon[x++] = gray;
						canvasFArrSon[x++] = gray;
						canvasFArrSon[x++] = gray;
						if (x == width * 4) {
							this.canvasFArr[y++] = canvasFArrSon;
							canvasFArrSon = new Uint8ClampedArray(width * 4);
							x = 0;
						}

					}
					// 将修改后的代码复制回画布中
					ctx.putImageData(imageData, 0, 0);
					callback && callback(canvas)
				};
			},
			/**
			 * 里图：调整图片亮度
			 * @param {Object} Light
			 * @param {Object} imgUrl
			 * @param {Object} callback
			 */
			GetGrayScaleHalf(Light, imgUrl, callback) {
				var img = new Image();
				img.src = imgUrl;
				var canvas = null;
				img.onload = function() {
					var width = img.width;
					var height = img.height;
					canvas = document.createElement('canvas');
					var ctx = canvas.getContext('2d');
					canvas.width = width;
					canvas.height = height;
					// 将源图片复制到画布上
					ctx.drawImage(img, 0, 0, width, height);
					// 获取画布的像素信息
					var imageData = ctx.getImageData(0, 0, width, height);
					var data = imageData.data;
					// 对像素集合中的单个像素进行循环，每个像素是由4个通道组成，所以要注意
					var i = 0;
					while (i < data.length) {
						var ri = data[i++],
							gi = data[i++],
							bi = data[i++],
							ai = data[i++];

						data[i - 4] = ri * Light;
						data[i - 3] = gi * Light;
						data[i - 2] = bi * Light;
						data[i - 1] = ai * Light;
					}
					// 将修改后的代码复制回画布中
					ctx.putImageData(imageData, 0, 0);
					callback && callback(canvas)
				};
			},
			/**
			 * 表图和里图合并
			 * @param {Object} colorsF
			 * @param {Object} colorsB
			 */
			GrayMergeHalf(colorsF, colorsB) {
				var ctxF = colorsF.getContext('2d');
				var ctxB = colorsB.getContext('2d');
				var imageDataF = ctxF.getImageData(0, 0, colorsF.width, colorsF.height);
				// var imageDataF = adjust(ctxF, colorsB.height, colorsB.width, heightF, widthF);
				var dataF = imageDataF.data;
				dataF = this.getnewDataArr(dataF, colorsF.width, colorsF.height, colorsB.width, colorsB.height);
				var imageDataB = ctxB.getImageData(0, 0, colorsB.width, colorsB.height);
				var dataB = imageDataB.data;
				var i = 0;
				var g = 0;
				var widthg = 0;
				var widthi = 0;
				while (g < dataB.length) {
					var rf = dataF[i++],
						gf = dataF[i++],
						bf = dataF[i++],
						af = dataF[i++];
					var rb = dataB[g++],
						gb = dataB[g++],
						bb = dataB[g++],
						ab = dataB[g++];

					var alpha = 255 - rf + rb;
					alpha = alpha > 255 ? 255 : alpha;
					alpha = alpha == 0 ? 1 : alpha;
					var newR = 255 * rb / alpha;
					newR = newR > 255 ? 255 : newR;
					var newG = 255 * gb / alpha;
					newG = newG > 255 ? 255 : newG;
					var newB = 255 * bb / alpha;
					newB = newB > 255 ? 255 : newB;

					dataB[g - 4] = newR;
					dataB[g - 3] = newG;
					dataB[g - 2] = newB;
					dataB[g - 1] = alpha;
				}
				// 将修改后的代码复制回画布中
				ctxB.putImageData(imageDataB, 0, 0);
				// 图片导出为 png 格式
				var type = 'png';
				return colorsB.toDataURL(type);
			},
			getnewDataArr(dataF, w, h, newW, newH) {
				var dw = w / newW
				var dh = h / newH
				var newarr = new Uint8ClampedArray(newH * newW * 4);
				var cw = 0;
				var ch = 0;
				var cd = 0;
				var start = 0;
				var end = 0;
				var x = 0;
				var y = 0;
				if (w / h < newW / newH) {
					cw = Math.trunc(w / dh);
					ch = newH;
					cd = dh
					start = (newW - cw) / 2
					end = start + cw
					var i = 0;
					var flag = false
					while (i < newH * newW * 4) {
						if ((i / 4) % newW <= start || (i / 4) % newW > end) {
							newarr[i++] = 255;
							newarr[i++] = 255;
							newarr[i++] = 255;
							newarr[i++] = 255;
							flag = true
						} else {
							var x1 = Math.trunc(x * cd)
							var y1 = Math.trunc(y * cd)
							try {
								newarr[i++] = this.canvasFArr[y1][x1++];
								newarr[i++] = this.canvasFArr[y1][x1++];
								newarr[i++] = this.canvasFArr[y1][x1++];
								newarr[i++] = this.canvasFArr[y1][x1++];
							} catch {}

							x = x + 4;
							if (flag) {
								y++;
								x = 0;
								flag = false
							}
						}
					}
				} else if (w / h > newW / newH) {
					cw = newW;
					ch = h / dw;
					cd = dw;
					start = (newH - ch) / 2
					end = newH - (newH - ch) / 2
					var i = 0;
					while (i < newH * newW * 4) {
						if ((i / 4) / newW <= start || (i / 4) / newW > end) {
							newarr[i++] = 255;
							newarr[i++] = 255;
							newarr[i++] = 255;
							newarr[i++] = 255;
						} else {
							var x1 = Math.trunc(x * cd)
							var y1 = Math.trunc(y * cd)
							try {
								newarr[i++] = this.canvasFArr[y1][x1++];
								newarr[i++] = this.canvasFArr[y1][x1++];
								newarr[i++] = this.canvasFArr[y1][x1++];
								newarr[i++] = this.canvasFArr[y1][x1++];
							} catch {}
							x = x + 4;
							if ((i / 4) % newW == 0) {
								y++;
								x = 0;
							}
						}
					}
				} else {
					cw = newW;
					ch = newH;
					cd = dw;
					var g = 0;
					var i = 0;
					while (i < newH * newW * 4) {
						var z = g * cd;
						newarr[i++] = dataF[z];
						newarr[i++] = dataF[z + 1];
						newarr[i++] = dataF[z + 2];
						newarr[i++] = dataF[z + 3];
						g++;
					}
				}
				return newarr;
			}
		}
	}
</script>

<style>
	#myimgtank>img {
		height: 300px;
		width: auto;
	}
</style>
