<template>
	<div>


		<el-col :span="8" v-for="(item,index) in m3u8Arr" style="padding: 10px; max-height: 500px;min-height: 100px;">
			<div class="grid-content bg-purple">
				<el-card :body-style="{ padding: '0px' }">
					<!-- <img src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png" class="image"> -->
					<img :src="m3u8ImgArr[index]" class="image" style="max-width: 90%;min-width: 50%; max-height: 400px;min-height: 80px;">
					<div style="padding: 14px;">
						<span>好吃的汉堡</span>
						<div class="bottom clearfix">
							<time class="time">{{ currentDate }}</time>
							<el-button type="text" class="button" @click="test(item)">操作按钮</el-button>
						</div>
					</div>
				</el-card>
			</div>
		</el-col>

	</div>

</template>

<script>
	import VideoCom from "@/components/VideoCom.vue";

	export default {
		components: {
			VideoCom
		},
		data() {
			return {
				player: null,
				m3u8Arr: [],
				m3u8ImgArr: [],
			}
		},
		beforeCreate() {},
		mounted() {
			let other = this
			this.$http({
				method: "get",
				url: "https://2296429409.github.io/app_m3u8/video.json",
				// url: "http://127.0.0.1:8848/my-personal-website/src/video/video.json",
				headers: {
					'Content-Type': 'application/json;charset=UTF-8'
				}
			}).then(function(response) {
				other.m3u8Arr = response.data.m3u8Arr
				other.m3u8ImgArr = response.data.m3u8ImgArr
			}).catch(function(error) {
				console.log(error);
			});
		},
		methods: {
			test(urlpath) {
				this.$router.push({
					path: '/Video',
					query: {
						url: urlpath,
					}
				})
			}
		}
	}
</script>