<template>
	<div>
		<p>tesetsetettetstestetstes {{url}}</p>
		<VideoCom :url="url" msg="Welcome to Your Vue.js App" />
	</div>

</template>

<script>
	import VideoCom from "@/components/VideoCom.vue";

	export default {
		components: {
			VideoCom
		},
		data() {
			return {
				url: null,
			}
		},
		created() {
			this.initData();
		},
		beforeCreate() {},
		mounted() {

		},
		methods: {
			// 初始化数据
			initData() {
				this.url = this.$route.query && this.$route.query.url;
			},
		}
	}
</script>